.shiny-text {
  color: rgba(0, 0, 0, 0);
  letter-spacing: .5px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, .05);
  background-image: linear-gradient(45deg, #2b83ff, #6a11cb, #2575fc, #4481eb);
  background-size: 300%;
  -webkit-background-clip: text;
  background-clip: text;
  font-weight: 600;
  animation: 8s infinite textGradient;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.shiny-text.theme-blue {
  background-image: linear-gradient(45deg, #2b83ff, #6a11cb, #2575fc, #4481eb);
}

.shiny-text.theme-purple {
  background-image: linear-gradient(45deg, #667eea, #764ba2, #b06ab3, #9d50bb);
}

.shiny-text.theme-green {
  background-image: linear-gradient(45deg, #11998e, #38ef7d, #2dd4bf, #10b981);
}

.shiny-text.theme-rainbow {
  background-image: linear-gradient(45deg, #ff0080, #ff8c00, #40e0d0, #9d50bb, #ff0080);
  background-size: 400%;
}

.shiny-text:after {
  content: "";
  width: 120%;
  height: 120%;
  animation: shine var(--animation-duration, 5s) cubic-bezier(.25, .1, .25, 1) infinite;
  z-index: 1;
  pointer-events: none;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .1) 10%, rgba(255, 255, 255, .6) 50%, rgba(255, 255, 255, .1) 90%, rgba(255, 255, 255, 0) 100%);
  position: absolute;
  top: -10%;
  left: -150%;
  transform: skewX(-20deg)translateY(0);
}

.shiny-text.disabled {
  color: #000;
  background: none;
  font-weight: normal;
  animation: none;
}

.shiny-text.disabled:after {
  animation: none;
  display: none;
}

[data-theme="dark"] .shiny-text {
  color: rgba(0, 0, 0, 0);
  -webkit-text-fill-color: transparent;
  text-shadow: none;
  background-image: linear-gradient(45deg, #a78bfa, #c084fc, #e879f9, #c084fc);
  background-size: 300%;
  -webkit-background-clip: text;
  background-clip: text;
  animation: 8s infinite textGradient;
}

[data-theme="dark"] .shiny-text:after {
  background: linear-gradient(90deg, rgba(197, 184, 255, 0) 0%, rgba(197, 184, 255, .05) 10%, rgba(197, 184, 255, .15) 50%, rgba(197, 184, 255, .05) 90%, rgba(197, 184, 255, 0) 100%);
}

@keyframes shine {
  0% {
    opacity: .7;
    left: -150%;
  }

  20% {
    opacity: 1;
  }

  80% {
    opacity: 1;
  }

  100% {
    opacity: .7;
    left: 250%;
  }
}

@keyframes textGradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

