body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Noto Sans, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  font-size: 14px;
}

h3 {
  text-transform: capitalize;
}

.app-container {
  background-color: #f5f5f5;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  display: flex;
}

.app-content {
  height: 100vh;
  overflow: hidden;
}

.app-grid-layout {
  height: 100%;
  display: flex;
}

.app-grid-layout .ant-row {
  flex-wrap: nowrap;
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
}

.app-panel {
  background-color: #fff;
  border-radius: 0;
  height: 100%;
  transition: box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 1px 2px #0000000d;
}

.app-panel:hover {
  box-shadow: 0 2px 8px #00000017;
}

.app-panel.left-panel {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.app-panel.right-panel {
  border-radius: 0;
  flex: 1;
  overflow: hidden;
  box-shadow: -4px 0 20px #0000000a;
}

.panel-content {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: auto;
}

.panel-content.left-panel-content {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.panel-content.left-panel-content .playground-panel-header {
  border-bottom: 1px solid #0000000f;
  flex-shrink: 0;
  align-items: center;
  height: 60px;
  padding: 0 24px;
  display: flex;
}

.panel-content.left-panel-content .playground-panel-header .header-row {
  justify-content: space-between;
  align-items: center;
  gap: 10px;
  width: 100%;
  display: flex;
}

.panel-content.left-panel-content .playground-panel-header h2 {
  color: #1890ff;
  margin: 0 0 16px;
  font-size: 18px;
  font-weight: 600;
}

.panel-content.left-panel-content .playground-panel-playground {
  flex-direction: column;
  flex: 1;
  min-height: 0;
  padding: 0 24px;
  display: flex;
}

.panel-content.left-panel-content .playground-panel-playground .playground-container {
  background: #fff;
  border: none;
  border-radius: 8px;
  flex: 1;
  overflow: hidden;
}

.panel-content.right-panel-content {
  border-radius: 0;
  padding: 0 24px 24px;
}

.server-offline-container {
  background: #f5f5f5;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.server-offline-container .server-offline-message {
  background: #f2f4f7;
  border-radius: 8px;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 14px 24px 0;
  display: flex;
  box-shadow: 0 2px 8px #0000000f;
}

.server-offline-container .server-offline-message .server-offline-content {
  text-align: center;
  flex-direction: column;
  flex: 1;
  justify-content: flex-start;
  align-items: center;
  padding-top: 20vh;
  display: flex;
}

.server-offline-container .server-offline-message .server-offline-icon {
  display: inline-block;
  position: relative;
}

.server-offline-container .server-offline-message .server-offline-icon .icon-background, .server-offline-container .server-offline-message .server-offline-icon .icon-foreground {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.server-offline-container .server-offline-message .server-offline-icon .icon-background {
  z-index: 1;
  width: 300px;
  height: 212px;
}

.server-offline-container .server-offline-message .server-offline-icon .icon-foreground {
  z-index: 2;
  width: 134px;
  height: 101px;
}

.server-offline-container .server-offline-message .server-offline-icon:after {
  content: "";
  width: 300px;
  height: 212px;
  display: block;
  position: relative;
}

.server-offline-container .server-offline-message h1 {
  letter-spacing: 0%;
  color: #000;
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.server-offline-container .server-offline-message .connection-status {
  letter-spacing: 0%;
  text-align: center;
  margin-top: 8px;
  font-size: 12px;
}

.panel-resize-handle {
  background-color: #f0f0f0;
  transition: background-color .2s;
  position: relative;
}

.panel-resize-handle.horizontal {
  cursor: col-resize;
  width: 1px;
}

.panel-resize-handle.vertical {
  cursor: row-resize;
  height: 1px;
}

.panel-resize-handle:hover, .panel-resize-handle:active, .panel-resize-handle[data-resize-handle-active] {
  background-color: #1677ff;
}

.clear-button-container {
  top: 20px !important;
  right: -4px !important;
}
